'use client';

import { FormEvent, ChangeEvent } from 'react';
import { Loader2, UploadCloud } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import {
    Dialog, DialogContent, DialogFooter, DialogHeader, DialogTitle, DialogDescription,
} from '@/components/ui/dialog';
import { Supplier } from '@/types';

interface SupplierFormDialogProps {
    isOpen: boolean;
    onOpenChange: (open: boolean) => void;
    onSubmit: () => void;
    formData: {
        nama_supplier: string;
        kontak_person: string;
        nomor_telepon: string;
        alamat: string;
        email: string;
        foto: File | null;
    };
    setFormData: (data: any) => void;
    initialData: Supplier | null;
    onFileChange: (e: ChangeEvent<HTMLInputElement>) => void;
    // Add isSaving here
    isSaving: boolean; 
}

export default function SupplierFormDialog({
    isOpen,
    onOpenChange,
    onSubmit,
    formData,
    setFormData,
    initialData,
    onFileChange,
    isSaving // Destructure isSaving
}: SupplierFormDialogProps) {
    
    const handleSubmit = (e: FormEvent) => {
        e.preventDefault();
        onSubmit();
    };

    return (
        <Dialog open={isOpen} onOpenChange={onOpenChange}>
            <DialogContent className="sm:max-w-[500px]">
                <DialogHeader>
                    <DialogTitle>{initialData ? 'Edit Supplier' : 'Tambah Supplier Baru'}</DialogTitle>
                    <DialogDescription>
                        {initialData ? 'Perbarui informasi supplier di bawah ini.' : 'Masukkan detail supplier baru.'}
                    </DialogDescription>
                </DialogHeader>

                <form onSubmit={handleSubmit} className="space-y-4 mt-2">
                    <div className="space-y-2">
                        <Label htmlFor="nama">Nama Supplier <span className="text-red-500">*</span></Label>
                        <Input 
                            id="nama" 
                            placeholder="PT. Sumber Rejeki" 
                            value={formData.nama_supplier}
                            onChange={(e) => setFormData((prev: any) => ({...prev, nama_supplier: e.target.value}))}
                            required
                        />
                    </div>

                    <div className="grid grid-cols-2 gap-4">
                        <div className="space-y-2">
                            <Label htmlFor="kontak">Kontak Person</Label>
                            <Input 
                                id="kontak" 
                                placeholder="Bpk. Budi" 
                                value={formData.kontak_person}
                                onChange={(e) => setFormData((prev: any) => ({...prev, kontak_person: e.target.value}))}
                            />
                        </div>
                        <div className="space-y-2">
                            <Label htmlFor="hp">No. HP / Telp</Label>
                            <Input 
                                id="hp" 
                                placeholder="0812..." 
                                value={formData.nomor_telepon}
                                onChange={(e) => setFormData((prev: any) => ({...prev, nomor_telepon: e.target.value}))}
                            />
                        </div>
                    </div>

                    <div className="space-y-2">
                        <Label htmlFor="email">Email</Label>
                        <Input 
                            id="email" 
                            type="email"
                            placeholder="email@supplier.com" 
                            value={formData.email}
                            onChange={(e) => setFormData((prev: any) => ({...prev, email: e.target.value}))}
                        />
                    </div>
                    
                    <div className="space-y-2">
                        <Label htmlFor="alamat">Alamat</Label>
                        <Textarea 
                            id="alamat" 
                            placeholder="Alamat lengkap..." 
                            value={formData.alamat}
                            onChange={(e) => setFormData((prev: any) => ({...prev, alamat: e.target.value}))}
                            className="resize-none h-20"
                        />
                    </div>
                    
                    <div className="space-y-2">
                        <Label htmlFor="icon">Foto Supplier</Label>
                        <div className="border-2 border-dashed border-gray-200 rounded-lg p-4 hover:bg-gray-50 transition-colors text-center cursor-pointer relative">
                            <input 
                                id="icon" 
                                type="file" 
                                accept="image/*"
                                onChange={onFileChange}
                                className="absolute inset-0 w-full h-full opacity-0 cursor-pointer"
                            />
                            <div className="flex flex-col items-center justify-center gap-1 text-sm text-gray-500">
                                <UploadCloud className="w-8 h-8 text-gray-400 mb-1" />
                                <span className="font-medium text-gray-700">Klik untuk upload gambar</span>
                                <span className="text-xs text-gray-400">JPG, PNG (Max 2MB)</span>
                                {formData.foto && (
                                    <span className="text-xs text-green-600 font-bold mt-1 bg-green-50 px-2 py-0.5 rounded">
                                        File terpilih: {formData.foto.name}
                                    </span>
                                )}
                            </div>
                        </div>
                    </div>

                    <DialogFooter className="pt-2">
                        <Button type="button" variant="outline" onClick={() => onOpenChange(false)}>
                            Batal
                        </Button>
                        <Button type="submit" disabled={isSaving} className="bg-[#D93F21] hover:bg-[#b9351b]">
                            {isSaving && <Loader2 className="mr-2 h-4 w-4 animate-spin" />} 
                            {initialData ? 'Perbarui' : 'Simpan'}
                        </Button>
                    </DialogFooter>
                </form>
            </DialogContent>
        </Dialog>
    );
}