'use client';

import { useState, useEffect, ChangeEvent } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';
import { Supplier } from '@/types';

export function useSupplierPage() {
    // State Data
    const [suppliers, setSuppliers] = useState<Supplier[]>([]);
    const [loading, setLoading] = useState(true);
    
    // Filter & Pagination
    const [searchQuery, setSearchQuery] = useState('');
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(10);

    // Modal & Form State
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    const [isSaving, setIsSaving] = useState(false);
    const [editData, setEditData] = useState<Supplier | null>(null);
    const [formData, setFormData] = useState({
        nama_supplier: '',
        kontak_person: '',
        nomor_telepon: '',
        alamat: '',
        email: '',
        foto: null as File | null,
    });

    // 1. Fetch Data
    const fetchSuppliers = async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/suppliers');
            setSuppliers(response.data);
        } catch (error) {
            console.error("Error fetching data:", error);
            toast.error("Gagal memuat data supplier.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchSuppliers();
    }, []);

    // 2. Filter Logic
    const filteredSuppliers = suppliers.filter((item) => {
        const searchLower = searchQuery.toLowerCase();
        return item.nama_supplier.toLowerCase().includes(searchLower) ||
               (item.kontak_person && item.kontak_person.toLowerCase().includes(searchLower));
    });

    // Pagination Logic
    useEffect(() => { setCurrentPage(1); }, [searchQuery]);
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = filteredSuppliers.slice(indexOfFirstItem, indexOfLastItem);

    // 3. Handlers
    const resetForm = () => {
        setFormData({ 
            nama_supplier: '', 
            kontak_person: '', 
            nomor_telepon: '', 
            alamat: '', 
            email: '', 
            foto: null 
        });
        setEditData(null);
        setIsDialogOpen(false);
    };

    const handleOpenDialog = (supplier?: Supplier) => {
        if (supplier) {
            setEditData(supplier);
            setFormData({
                nama_supplier: supplier.nama_supplier,
                kontak_person: supplier.kontak_person || '',
                nomor_telepon: supplier.nomor_telepon || '',
                alamat: supplier.alamat || '',
                email: supplier.email || '',
                foto: null
            });
        } else {
            resetForm();
        }
        setIsDialogOpen(true);
    };

    const handleFileChange = (e: ChangeEvent<HTMLInputElement>) => {
        if (e.target.files && e.target.files[0]) {
            setFormData(prev => ({ ...prev, foto: e.target.files![0] }));
        }
    };

    const handleSubmit = async () => {
        setIsSaving(true);
        try {
            if (editData) {
                // UPDATE
                await axios.put(`/api/suppliers/${editData.id}`, {
                    nama_supplier: formData.nama_supplier,
                    kontak_person: formData.kontak_person,
                    nomor_telepon: formData.nomor_telepon,
                    alamat: formData.alamat,
                    email: formData.email
                });
                
                if (formData.foto) {
                    const imgData = new FormData();
                    imgData.append('foto', formData.foto);
                    await axios.post(`/api/suppliers/${editData.id}/foto`, imgData, {
                        headers: { 'Content-Type': 'multipart/form-data' },
                    });
                }
                toast.success("Supplier berhasil diperbarui!");
            } else {
                // CREATE
                const data = new FormData();
                Object.keys(formData).forEach(key => {
                    // @ts-ignore - Dynamic key access
                    const value = formData[key];
                    if (value !== null && value !== '') {
                        data.append(key, value);
                    }
                });

                await axios.post('/api/suppliers', data, {
                    headers: { 'Content-Type': 'multipart/form-data' },
                });
                toast.success("Supplier berhasil ditambahkan!");
            }
            
            setIsDialogOpen(false);
            resetForm();
            fetchSuppliers();
            
        } catch (error: any) {
            console.error(error);
            const message = error.response?.data?.message || "Gagal menyimpan data.";
            toast.error(message);
        } finally {
            setIsSaving(false);
        }
    };

    const handleDelete = async (id: number) => {
        if(!confirm("Yakin ingin menghapus supplier ini?")) return;
        try {
            await axios.delete(`/api/suppliers/${id}`);
            toast.success("Supplier dihapus");
            fetchSuppliers();
        } catch (error) {
            toast.error("Gagal menghapus.");
        }
    };

    return {
        // Data & State
        suppliers: currentItems,
        totalData: filteredSuppliers.length,
        loading,
        currentPage,
        itemsPerPage,
        searchQuery,
        
        // Modal & Form State
        isDialogOpen,
        isSaving,
        editData,
        formData,

        // Setters
        setSearchQuery,
        setCurrentPage,
        setFormData,
        setIsDialogOpen,

        // Functions
        handleOpenDialog,
        handleFileChange,
        handleSubmit,
        handleDelete,
        resetForm
    };
}