'use client';

import { Plus } from 'lucide-react';
import { Button } from '@/components/ui/button';

// Import Custom Hook
import { useSupplierPage } from '@/hooks/admin/useSupplierPage';

// Import Komponen UI
import SupplierFilters from '@/components/admin/supplier/SupplierFilter';
import SupplierTable from '@/components/admin/supplier/SupplierTabel';
import SupplierFormDialog from '@/components/admin/supplier/SupplierFormDialog';

export default function SupplierPage() {
    const {
        // Data
        suppliers,
        totalData,
        loading,
        
        // Filter & Pagination
        currentPage,
        itemsPerPage,
        searchQuery,
        setSearchQuery,
        setCurrentPage,
        
        // State Modal & Form
        isDialogOpen,
        setIsDialogOpen,
        isSaving,
        editData,
        formData,
        setFormData,

        // Handlers
        handleOpenDialog,
        handleFileChange,
        handleSubmit,
        handleDelete
    } = useSupplierPage();

    return (
        <div className="space-y-6">
            {/* Header Section */}
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Data Supplier</h1>
                    <p className="text-gray-500 text-sm">Kelola data pemasok barang dan bahan baku.</p>
                </div>
                
                <Button 
                    className="bg-[#D93F21] hover:bg-[#b9351b] shadow-sm" 
                    onClick={() => handleOpenDialog()}
                >
                    <Plus className="w-4 h-4 mr-2" /> Tambah Supplier
                </Button>
            </div>

            {/* Filter */}
            <SupplierFilters 
                searchQuery={searchQuery}
                setSearchQuery={setSearchQuery}
            />

            {/* Tabel Data */}
            <SupplierTable 
                loading={loading}
                suppliers={suppliers}
                totalData={totalData}
                currentPage={currentPage}
                itemsPerPage={itemsPerPage}
                onPageChange={setCurrentPage}
                onEdit={handleOpenDialog}
                onDelete={handleDelete}
            />

            {/* Modal Form */}
            <SupplierFormDialog 
                isOpen={isDialogOpen}
                onOpenChange={setIsDialogOpen}
                onSubmit={handleSubmit}
                isSaving={isSaving}
                formData={formData}
                setFormData={setFormData}
                initialData={editData}
                onFileChange={handleFileChange}
            />
        </div>
    );
}